import React, { Component } from 'react';
import './App.css';
import {AiFillCaretDown} from 'react-icons/ai';
import {AiFillGithub} from 'react-icons/ai';
import {FiSearch} from 'react-icons/fi';
import {SiBootstrap} from 'react-icons/si';
import {AiFillHtml5} from 'react-icons/ai';
import {FaCss3} from 'react-icons/fa';
import {IoLogoJavascript} from 'react-icons/io';
import {FaJava} from 'react-icons/fa';
import {IoLogoPython} from 'react-icons/io';
import {SiDjango} from 'react-icons/si';
import {SiFirebase} from 'react-icons/si';
import {FaMapMarked} from 'react-icons/fa';
import {MdEmail} from 'react-icons/md';
import {SiPostgresql} from 'react-icons/si';
import {SiFlask} from 'react-icons/si';
import {SiJquery} from 'react-icons/si';
import {SiCplusplus} from 'react-icons/si';
import pet_online_shop from './pet-online-shop.jpg';
import e_voucher from './e_voucher.jpg';
import multiplication_game from './multiplication_game.jpg';
import homepage from './homepage.jpg';
import atm from './atm.jpg';
import CookGame from './CookGame.jpg';
import Flickity from 'react-flickity-component';

const flickityOptions = {
    groupCells: 3,
    autoPlay: true,
    freeScroll: true,
    wrapAround: true,
    initialIndex: 2,
    setGallerySize: false,
    pageDots: false,
    arrowShape: { 
        x0: 0,
        x1: 0, y1: 0,
        x2: 0, y2: 0,
        x3: 0
      }
}


class Project extends React.Component{

    constructor(props) {
        super(props);
    }

    project = {
        width: '330px',
        height: '310px',
        borderRadius: '10px',
        padding: '10px',
        boxShadow: '0 3px 3px 0 gray',
        margin:'20px',
        position: 'relative',
    }

    image = {
        width: '100%',
        height: '50%',
        borderRadius: '10px',
        boxShadow: '0 3px 3px 0 gray',
    }

    title = {
        color:'#52050A',
        fontSize: '23px',
        margin:'8px 0'
    }

    description = {
        color:"#2F3544",
        fontSize: '18px',
        margin:'0px'
    }

    date = {
        fontSize:'14px',
        color:"#2F3544",
        opacity:'0.75',
        position: 'absolute',
        bottom: '0px',
        right:'10px'
    }

    type = {
        fontSize:'17px',
        color:"#19647E",
        position: 'absolute',
        bottom: '-3px',
        left:'10px'
    }

    programming_language = {
        color:'white',
        fontSize: '19px',
        top: '43%',
        left: '10%'
    }

    lang = this.props.langs.map((lang)=>{
        return (<span className="lang">{lang}</span> );
    });

    render(){        
        return(
            
            <a style={this.project} className={`pj ${(this.props.isDisplay||this.props.category=="All")&&(this.props.search_words==""||this.props.title.toUpperCase().includes(this.props.search_words.toUpperCase()))?'':'isDisplayNone'}`}>
                <div className="card-front">
                    <img style={this.image} src={this.props.porject_img}></img>
                    <p style={this.title}>{this.props.title}</p>
                    <p style={this.description}>{this.props.description}</p>
                    <p style={this.type}>Category: {this.props.type}</p>
                    <p style={this.date}>{this.props.date}</p>
                </div>
                <a target="_blank" rel="noopener noreferrer" href={this.props.github_link} className="card-back github"><AiFillGithub /></a>
                <p className="card-back" style={this.programming_language}>Programming languages &amp; Tools</p>
                <div className="card-back langs">
                    {this.lang}     
                </div>     
            </a>

        );
        
    }

}

class Container extends React.Component{

    constructor(props) {
        super(props);
        this.state = {category_isDisplayNone:true,orderBy_isDisplayNone:true,search_focus:false,category:'All',order_by:'Latest',search_words:''};
    
        this.search_change = this.search_change.bind(this);
    }

    projects = {
        display: 'flex',
        width: '90%',
        margin: '35px auto'
    }

    contain_style = {
        width: '90%',
        margin: 'auto',
        marginTop: '50px'
    }
    category_label = {
        opacity : '0.75',
        color: "#2F3544",
        fontSize: '1.5rem'
    }
    category = {
        fontSize: '1.2rem',
        border: 'none',
        color: '#52050A',
        backgroundColor: 'transparent',
        cursor: 'pointer'
    }

    option = {
        backgroundColor:'white',
        padding: '10px',
        marginBottom: '10px',
        marginRight: '20px',
        borderRadius: '25px',
        display: 'inline-block'
    }

    dropdown_element = {
        color: 'initial',
        backgroundColor: 'white',
        fontFamily: 'sans-serif',
        position: 'absolute',
        top:'25px',
        left:'0',
        fontSize: '1.2rem',
        width: '130px',
        border: '1px solid gray',
        borderRadius: '5px',
        boxShadow: '0 3px 3px 0 gray',
        zIndex: '2'
    }
    
    search_focus = {
        backgroundColor:'white',
        padding: '10px',
        fontFamily: 'sans-serif',
        fontSize: '17px',
        borderRadius: '7px',
        opacity: '0.75',
        width: '300px',
        border: '1px solid #52050A',
        outline: 'none',
        borderCollapse: 'collapse',
        transition: 'width 0.5s ease-in-out'
    }

    search_blur = {
        backgroundColor:'white',
        padding: '10px',
        fontFamily: 'sans-serif',
        fontSize: '17px',
        borderRadius: '7px',
        opacity: '0.75',
        width: '150px',
        border: '1px groove #2F3544',
        borderCollapse: 'collapse',
        transition: 'width 0.5s ease-in-out'
    }

    search = {
        backgroundColor:'white',
        padding: '7px',
        border: 'none',
        borderLeft: '1px groove #2F3544',
        fontSize: '17px',
        textAlign: 'center',
        position: 'absolute',
        top:'2px',
        right:'4px',
        borderCollapse: 'collapse'
    }

    input_container={
        position:'relative',
        float: 'right'
    }

    category_show_dropdown_element=()=>  {
        this.setState({category_isDisplayNone:!this.state.category_isDisplayNone});
    }
    orderBy_show_dropdown_element=()=>  {
        this.setState({orderBy_isDisplayNone:!this.state.orderBy_isDisplayNone});
    }
    
    search_focus_in=()=>{
        this.setState({search_focus:true});
    }

    search_focus_out=()=>{
        this.setState({search_focus:false});
    }

    set_category=(c)=>{
        this.setState({category:c});
        this.category_show_dropdown_element();
          
    }

    set_order_by=(o)=>{
        this.setState({order_by:o});
        this.orderBy_show_dropdown_element();

    }

    shouldComponentUpdate() {
        return true;
    }

    search_change(event) {
        this.setState({search_words:event.target.value});
    }

    render(){
        const category_isDisplayNone = this.state.category_isDisplayNone;
        const orderBy_isDisplayNone = this.state.orderBy_isDisplayNone;
        const search_focus = this.state.search_focus;
        return(
            
            <div>
                <div style={this.contain_style}>
                    <span style={this.option}>
                        <label for="category" style={this.category_label}>Category: </label>
                        <span style={{position:'relative'}}>
                            <button id='dropdown' names='category' style={this.category} onClick={this.category_show_dropdown_element}>{this.state.category} <AiFillCaretDown /></button>
                            <div id='dropdown_element' style={this.dropdown_element} className={category_isDisplayNone?'isDisplayNone':''} onMouseLeave={!category_isDisplayNone?this.category_show_dropdown_element:''}>
                                <a onClick={()=>this.set_category('All')}>All</a>
                                <a onClick={()=>this.set_category('Web')}>Web</a>
                                <a onClick={()=>this.set_category('Java')}>Java</a>
                                <a onClick={()=>this.set_category('C++')}>C++</a>
                            </div>
                        </span>
                    </span>
                    <span style={this.option}>
                        <label for="category" style={this.category_label}>Order By: </label>
                        <span style={{position:'relative'}}>
                            <button id='dropdown' names='category' style={this.category} onClick={this.orderBy_show_dropdown_element} >{this.state.order_by} <AiFillCaretDown /></button>
                            <div id='dropdown_element' style={this.dropdown_element} className={orderBy_isDisplayNone?'isDisplayNone':''} onMouseLeave={!orderBy_isDisplayNone?this.orderBy_show_dropdown_element:''}>
                                <a onClick={()=>this.set_order_by('Latest')}>Latest</a>
                                <a onClick={()=>this.set_order_by('Oldest')}>Oldest</a>
                            </div>   
                        </span>
                    </span>

                    <span style={this.input_container}>
                            <input value={this.state.search_words} onChange={this.search_change} onFocus={this.search_focus_in} onBlur={this.search_focus_out} type='text' style={search_focus?this.search_focus:this.search_blur} placeholder='Find a project'></input>
                            <button style={this.search}><FiSearch /></button>
                    </span>

                </div>

                <div id="projects" style={this.projects} className={this.state.order_by=="Latest"?'not_reverse':"reverse"}>
                    <Project github_link="https://github.com/timyiu478/secure-e-voucher-system" porject_img={e_voucher} title="Secure E-Voucher System" description="A web-based secure e-voucher system." date="03-2021" category={this.state.category} type="Web" isDisplay={"Web"===this.state.category} search_words={this.state.search_words} langs={[<AiFillHtml5 />,<FaCss3 />,<IoLogoJavascript />,<SiJquery />,<IoLogoPython />,<SiDjango />,<SiFlask />,<SiPostgresql />]} />
                    <Project github_link="https://github.com/timyiu478/pet-online-shop" porject_img={pet_online_shop} title="Pet Online Shop" description="This website allows customer buy pet products over the internet." date="02-2021" type="Web" category={this.state.category} isDisplay={"Web"===this.state.category} search_words={this.state.search_words} langs={[<AiFillHtml5 />,<FaCss3 />,<IoLogoJavascript />,<SiJquery />,<SiBootstrap />,<SiFirebase />,<FaMapMarked />,<MdEmail />]} />
                    <Project github_link="https://github.com/timyiu478/comp3423ass1.github.io" porject_img={multiplication_game} title="Multiplication Game" description="A Game for Children learning the multiplication tables." date="01-2021" type="Web" category={this.state.category} isDisplay={"Web"===this.state.category} search_words={this.state.search_words} langs={[<AiFillHtml5 />,<FaCss3 />,<IoLogoJavascript />,<SiBootstrap />]} />
                    <Project github_link="https://github.com/timyiu478/homepage" porject_img={homepage} title="Personal Homepage" description="My personal homepage. Also, it is my first webpage." date="12-2020" category={this.state.category} type="Web" isDisplay={"Web"===this.state.category} search_words={this.state.search_words} langs={[<AiFillHtml5 />,<FaCss3 />,<IoLogoJavascript />,<SiJquery />,<SiFirebase />]} />
                    <Project github_link="https://github.com/timyiu478/ATM" porject_img={atm} title="ATM" description="ATM system simulates the real-life ATM in HK includes the interface, keypad, and operation." date="11-2019" category={this.state.category} type="Java" isDisplay={"Java"===this.state.category} search_words={this.state.search_words} langs={[<FaJava />]} />
                    <Project github_link="https://github.com/timyiu478/Cooking-Game" porject_img={CookGame} title="Cooking Game" description="A Cooking game that runs in command line environment." date="11-2018" category={this.state.category} type="C++" isDisplay={"C++"===this.state.category} search_words={this.state.search_words} langs={[<SiCplusplus />]} />    
                </div>
            </div>
        );
    }
}

var elem = document.querySelector('.main-gallery');
var flkty = new Flickity( elem, {
  // options
  groupCells:true
});

export default Container;